/**
 * File_ID 
 *  File_ID 
 *  pathValidator 
 */

/**
 * 
 * : \ : * ? " ' < > |  ( ) [ ] { } # % ^ ` ~ ; @ & = + $ ,
 *  pathValidator.js 
 */
const INVALID_CHARS = /[\\:\*\?"'<>\| \(\)\[\]\{\}#%\^`~;@&=\+\$,]/;

/**
 *  File_ID 
 * @param {string} newFileId -  File_ID
 * @param {string} currentFileId -  File_ID
 * @returns {{ valid: boolean, error?: string }}
 */
export function validateFileId(newFileId, currentFileId) {
  // 1. /
  if (!newFileId || newFileId.trim() === '') {
    return {
      valid: false,
      error: ''
    };
  }

  // 2.  File_ID 
  if (newFileId === currentFileId) {
    return {
      valid: false,
      error: ''
    };
  }

  // 3.  pathValidator 
  if (INVALID_CHARS.test(newFileId)) {
    return {
      valid: false,
      error: ''
    };
  }

  // 4. 
  if (newFileId.includes('//')) {
    return {
      valid: false,
      error: ''
    };
  }

  return { valid: true };
}
