import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.json.stringify.js";
import { ElMessage } from 'element-plus';
import { Loading } from '@element-plus/icons-vue';
import fetchWithAuth from '@/utils/fetchWithAuth';
export default {
  name: 'TagManagementDialog',
  components: {
    Loading
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    fileId: {
      type: String,
      required: true
    }
  },
  emits: ['update:modelValue', 'tagsUpdated'],
  data() {
    return {
      currentTags: [],
      inputTag: '',
      suggestions: [],
      popularTags: [],
      showSuggestions: false,
      loading: false,
      loadingPopularTags: false,
      debounceTimer: null
    };
  },
  computed: {
    visible: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit('update:modelValue', val);
      }
    },
    dialogWidth() {
      return window.innerWidth < 768 ? '90%' : '500px';
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.loadFileTags();
        this.loadPopularTags();
      }
    }
  },
  methods: {
    async loadFileTags() {
      try {
        const response = await fetchWithAuth(`/api/manage/tags/${encodeURIComponent(this.fileId)}`, {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.currentTags = data.tags || [];
        } else {
          throw new Error('Failed to load tags');
        }
      } catch (error) {
        console.error('Error loading file tags:', error);
        ElMessage.error('');
      }
    },
    async loadPopularTags() {
      this.loadingPopularTags = true;
      try {
        const response = await fetchWithAuth('/api/manage/tags/autocomplete?limit=20', {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.popularTags = (data.tags || []).filter(tag => !this.currentTags.includes(tag));
        }
      } catch (error) {
        console.error('Error loading popular tags:', error);
      } finally {
        this.loadingPopularTags = false;
      }
    },
    handleInputChange() {
      clearTimeout(this.debounceTimer);
      if (!this.inputTag || this.inputTag.trim().length === 0) {
        this.showSuggestions = false;
        return;
      }
      this.debounceTimer = setTimeout(() => {
        this.fetchSuggestions();
      }, 300);
    },
    async fetchSuggestions() {
      try {
        const prefix = this.inputTag.trim().toLowerCase();
        const response = await fetchWithAuth(`/api/manage/tags/autocomplete?prefix=${encodeURIComponent(prefix)}&limit=10`, {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.suggestions = (data.tags || []).filter(tag => !this.currentTags.includes(tag));
          this.showSuggestions = this.suggestions.length > 0;
        }
      } catch (error) {
        console.error('Error fetching suggestions:', error);
      }
    },
    selectSuggestion(tag) {
      this.inputTag = tag;
      this.showSuggestions = false;
      this.handleAddTag();
    },
    async handleAddTag() {
      const tag = this.inputTag.trim();
      if (!tag) {
        return;
      }
      if (this.currentTags.includes(tag)) {
        ElMessage.warning('');
        this.inputTag = '';
        this.showSuggestions = false;
        return;
      }
      try {
        const response = await fetchWithAuth(`/api/manage/tags/${encodeURIComponent(this.fileId)}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            action: 'add',
            tags: [tag]
          })
        });
        if (response.ok) {
          const data = await response.json();
          this.currentTags = data.tags || [];
          this.inputTag = '';
          this.showSuggestions = false;
          ElMessage.success('');
          this.$emit('tagsUpdated', this.currentTags);

          // 
          this.loadPopularTags();
        } else {
          const error = await response.json();
          throw new Error(error.message || '');
        }
      } catch (error) {
        console.error('Error adding tag:', error);
        ElMessage.error(error.message || '');
      }
    },
    async handleRemoveTag(tag) {
      try {
        const response = await fetchWithAuth(`/api/manage/tags/${encodeURIComponent(this.fileId)}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            action: 'remove',
            tags: [tag]
          })
        });
        if (response.ok) {
          const data = await response.json();
          this.currentTags = data.tags || [];
          ElMessage.success('');
          this.$emit('tagsUpdated', this.currentTags);

          // 
          this.loadPopularTags();
        } else {
          throw new Error('');
        }
      } catch (error) {
        console.error('Error removing tag:', error);
        ElMessage.error('');
      }
    },
    handleAddPopularTag(tag) {
      this.inputTag = tag;
      this.handleAddTag();
    },
    handleClose() {
      this.visible = false;
      this.inputTag = '';
      this.showSuggestions = false;
      this.currentTags = [];
      this.popularTags = [];
    }
  }
};