export default {
  name: 'MobileActionSheet',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    isFolder: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'action'],
  methods: {
    close() {
      this.$emit('update:modelValue', false);
    },
    handleAction(action) {
      this.$emit('action', action);
      this.close();
    }
  }
};