      mobileActionIndex: -1,
      // 
      mobileActionIsFolder: false,
      // 
      longPressTimer: null,
      // 
      showMobileDirectoryDrawer: false,
      // 
      jumpPage: '',
      // 
      // 
      filters: {
        accessStatus: [],
        // : 'normal'(), 'blocked'()
        listType: [],
        // : 'White', 'Block', 'None'
        label: [],
        // : 'normal', 'teen', 'adult'
        fileType: [],
        // : 'image', 'video', 'audio', 'other'
        channel: [],
        // : 'TelegramNew', 'CloudflareR2', 'S3', 'Discord', 'HuggingFace', 'External'
        channelName: [] // : 
      },
      channelNameOptions: [] // 
    };
  },
