import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.reduce.js";
export default {
  name: 'FilterDropdown',
  props: {
    filters: {
      type: Object,
      default: () => ({
        accessStatus: [],
        // : 'normal'(), 'blocked'()
        listType: [],
        // : 'White', 'Block', 'none'
        label: [],
        fileType: [],
        channel: [],
        channelName: []
      })
    },
    channelNameOptions: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:filters', 'change'],
  data() {
    return {
      //  ListType  Label
      // : ListType !== 'Block' && Label !== 'adult'
      // : ListType === 'Block' || Label === 'adult'
      accessStatusOptions: [{
        label: '',
        value: 'normal'
      }, {
        label: '',
        value: 'blocked'
      }],
      //  ListType 
      listTypeOptions: [{
        label: '',
        value: 'White'
      }, {
        label: '',
        value: 'Block'
      }, {
        label: '',
        value: 'None'
      }],
      // 
      //  FileDetailDialog: adult=, =
      labelOptions: [{
        label: '',
        value: 'normal'
      }, {
        label: '12+',
        value: 'teen'
      }, {
        label: '',
        value: 'adult'
      }],
      fileTypeOptions: [{
        label: '',
        value: 'image'
      }, {
        label: '',
        value: 'video'
      }, {
        label: '',
        value: 'audio'
      }, {
        label: '',
        value: 'other'
      }],
      channelOptions: [{
        label: 'Telegram',
        value: 'TelegramNew'
      }, {
        label: 'Cloudflare R2',
        value: 'CloudflareR2'
      }, {
        label: 'S3',
        value: 'S3'
      }, {
        label: 'Discord',
        value: 'Discord'
      }, {
        label: 'HuggingFace',
        value: 'HuggingFace'
      }, {
        label: '',
        value: 'External'
      }],
      localFilters: {
        accessStatus: [],
        listType: [],
        label: [],
        fileType: [],
        channel: [],
        channelName: []
      }
    };
  },
  computed: {
    activeFilterCount() {
      return Object.values(this.localFilters).reduce((count, arr) => count + arr.length, 0);
    },
    // 
    groupedChannelNames() {
      const groups = {};
      this.channelNameOptions.forEach(option => {
        if (!groups[option.type]) {
          groups[option.type] = {
            type: option.type,
            typeLabel: option.typeLabel,
            channels: []
          };
        }
        groups[option.type].channels.push(option);
      });
      return Object.values(groups);
    }
  },
  watch: {
    filters: {
      handler(newFilters) {
        this.localFilters = {
          accessStatus: Array.isArray(newFilters.accessStatus) ? [...newFilters.accessStatus] : [],
          listType: Array.isArray(newFilters.listType) ? [...newFilters.listType] : [],
          label: Array.isArray(newFilters.label) ? [...newFilters.label] : [],
          fileType: Array.isArray(newFilters.fileType) ? [...newFilters.fileType] : [],
          channel: Array.isArray(newFilters.channel) ? [...newFilters.channel] : [],
          channelName: Array.isArray(newFilters.channelName) ? [...newFilters.channelName] : []
        };
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    handleFilterChange(type) {
      this.$emit('update:filters', {
        ...this.localFilters
      });
      this.$emit('change', {
        type,
        filters: {
          ...this.localFilters
        }
      });
    },
    clearFilters() {
      this.localFilters = {
        accessStatus: [],
        listType: [],
        label: [],
        fileType: [],
        channel: [],
        channelName: []
      };
      this.$emit('update:filters', {
        ...this.localFilters
      });
      this.$emit('change', {
        type: 'clear',
        filters: {
          ...this.localFilters
        }
      });
    }
  }
};