import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.map.js";
import "core-js/modules/es.json.stringify.js";
import { ElMessage, ElMessageBox } from 'element-plus';
import fetchWithAuth from '@/utils/fetchWithAuth';
export default {
  name: 'BatchTagDialog',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    selectedFiles: {
      type: Array,
      required: true,
      default: () => []
    }
  },
  emits: ['update:modelValue', 'tagsUpdated'],
  data() {
    return {
      activeTab: 'add',
      inputTag: '',
      tagsToAdd: [],
      commonTags: [],
      suggestions: [],
      showSuggestions: false,
      loading: false,
      debounceTimer: null
    };
  },
  computed: {
    visible: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit('update:modelValue', val);
      }
    },
    dialogWidth() {
      return window.innerWidth < 768 ? '90%' : '600px';
    },
    selectedFilesOnly() {
      // 
      return this.selectedFiles.filter(file => !file.isFolder);
    },
    fileCount() {
      return this.selectedFilesOnly.length;
    },
    fileIds() {
      return this.selectedFilesOnly.map(file => file.name);
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.loadCommonTags();
      } else {
        this.resetData();
      }
    },
    activeTab(newTab) {
      if (newTab === 'remove') {
        this.loadCommonTags();
      }
    }
  },
  methods: {
    resetData() {
      this.tagsToAdd = [];
      this.inputTag = '';
      this.showSuggestions = false;
      this.activeTab = 'add';
    },
    async loadCommonTags() {
      if (this.selectedFilesOnly.length === 0) {
        this.commonTags = [];
        return;
      }
      try {
        // 
        const tagPromises = this.selectedFilesOnly.map(file => fetchWithAuth(`/api/manage/tags/${encodeURIComponent(file.name)}`, {
          method: 'GET'
        }));
        const responses = await Promise.all(tagPromises);
        const allTags = [];
        for (const response of responses) {
          if (response.ok) {
            const data = await response.json();
            allTags.push(data.tags || []);
          }
        }

        // 
        if (allTags.length > 0) {
          this.commonTags = allTags[0].filter(tag => allTags.every(tags => tags.includes(tag)));
        } else {
          this.commonTags = [];
        }
      } catch (error) {
        console.error('Error loading common tags:', error);
        ElMessage.error('');
      }
    },
    handleInputChange() {
      clearTimeout(this.debounceTimer);
      if (!this.inputTag || this.inputTag.trim().length === 0) {
        this.showSuggestions = false;
        return;
      }
      this.debounceTimer = setTimeout(() => {
        this.fetchSuggestions();
      }, 300);
    },
    async fetchSuggestions() {
      try {
        const prefix = this.inputTag.trim().toLowerCase();
        const response = await fetchWithAuth(`/api/manage/tags/autocomplete?prefix=${encodeURIComponent(prefix)}&limit=10`, {
          method: 'GET'
        });
        if (response.ok) {
          const data = await response.json();
          this.suggestions = (data.tags || []).filter(tag => !this.tagsToAdd.includes(tag));
          this.showSuggestions = this.suggestions.length > 0;
        }
      } catch (error) {
        console.error('Error fetching suggestions:', error);
      }
    },
    selectSuggestion(tag) {
      this.inputTag = tag;
      this.showSuggestions = false;
      this.handleAddInputTag();
    },
    handleAddInputTag() {
      const tag = this.inputTag.trim();
      if (!tag) {
        return;
      }
      if (this.tagsToAdd.includes(tag)) {
        ElMessage.warning('');
        this.inputTag = '';
        this.showSuggestions = false;
        return;
      }
      this.tagsToAdd.push(tag);
      this.inputTag = '';
      this.showSuggestions = false;
    },
    removeFromToAdd(tag) {
      const index = this.tagsToAdd.indexOf(tag);
      if (index > -1) {
        this.tagsToAdd.splice(index, 1);
      }
    },
    async executeAddTags() {
      if (this.tagsToAdd.length === 0) {
        ElMessage.warning('');
        return;
      }
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/tags/batch', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            fileIds: this.fileIds,
            action: 'add',
            tags: this.tagsToAdd
          })
        });
        if (response.ok) {
          const data = await response.json();
          if (data.success || data.updated > 0) {
            ElMessage.success(` ${data.updated} `);
            this.$emit('tagsUpdated');
            this.tagsToAdd = [];
          } else {
            throw new Error('');
          }
        } else {
          throw new Error('');
        }
      } catch (error) {
        console.error('Error adding tags:', error);
        ElMessage.error('');
      } finally {
        this.loading = false;
      }
    },
    async handleRemoveCommonTag(tag) {
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/tags/batch', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            fileIds: this.fileIds,
            action: 'remove',
            tags: [tag]
          })
        });
        if (response.ok) {
          const data = await response.json();
          if (data.success || data.updated > 0) {
            ElMessage.success(` ${data.updated} `);
            this.$emit('tagsUpdated');
            await this.loadCommonTags();
          } else {
            throw new Error('');
          }
        } else {
          throw new Error('');
        }
      } catch (error) {
        console.error('Error removing tag:', error);
        ElMessage.error('');
      } finally {
        this.loading = false;
      }
    },
    handleClearAllTags() {
      ElMessageBox.confirm(` ${this.fileCount} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.executeClearTags();
      }).catch(() => {
        ElMessage.info('');
      });
    },
    async executeClearTags() {
      this.loading = true;
      try {
        const response = await fetchWithAuth('/api/manage/tags/batch', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            fileIds: this.fileIds,
            action: 'set',
            tags: []
          })
        });
        if (response.ok) {
          const data = await response.json();
          if (data.success || data.updated > 0) {
            ElMessage.success(` ${data.updated} `);
            this.$emit('tagsUpdated');
            this.commonTags = [];
          } else {
            throw new Error('');
          }
        } else {
          throw new Error('');
        }
      } catch (error) {
        console.error('Error clearing tags:', error);
        ElMessage.error('');
      } finally {
        this.loading = false;
      }
    },
    handleClose() {
      this.visible = false;
    }
  }
};