export default {
  name: 'FileCard',
  props: {
    item: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    },
    fileLink: {
      type: String,
      required: true
    },
    previewSrcList: {
      type: Array,
      default: () => []
    },
    disableTooltip: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:selected', 'detail', 'copy', 'move', 'delete', 'download', 'touchstart', 'touchend', 'touchmove'],
  data() {
    return {
      localSelected: this.selected,
      videoLoading: true,
      videoError: false
    };
  },
  computed: {
    channelTag() {
      return this.item.channelTag || '';
    },
    isVideo() {
      //  content-type 
      const fileType = this.item.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('video')) return true;
      // 
      const name = this.item.name?.toLowerCase() || '';
      return name.endsWith('.mp4') || name.endsWith('.webm') || name.endsWith('.mov') || name.endsWith('.avi');
    },
    isAudio() {
      //  content-type 
      const fileType = this.item.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('audio')) return true;
      // 
      const name = this.item.name?.toLowerCase() || '';
      return name.endsWith('.mp3') || name.endsWith('.wav') || name.endsWith('.ogg') || name.endsWith('.flac');
    },
    isImage() {
      //  content-type 
      const fileType = this.item.metadata?.FileType?.toLowerCase() || '';
      if (fileType.includes('image')) return true;
      // 
      const name = this.item.name?.toLowerCase() || '';
      return name.endsWith('.jpg') || name.endsWith('.jpeg') || name.endsWith('.png') || name.endsWith('.gif') || name.endsWith('.webp') || name.endsWith('.svg') || name.endsWith('.bmp') || name.endsWith('.avif') || name.endsWith('.heic') || name.endsWith('.heif');
    },
    displayName() {
      const fileName = this.item.metadata?.FileName || this.item.name || '';
      const parts = fileName.split('/');
      return parts[parts.length - 1];
    }
  },
  watch: {
    selected(val) {
      this.localSelected = val;
    },
    fileLink() {
      // 
      this.videoLoading = true;
      this.videoError = false;
    }
  },
  methods: {
    handleVideoError() {
      this.videoLoading = false;
      this.videoError = true;
    },
    handleVideoClick(e) {
      const video = e.target;
      if (video.requestFullscreen) {
        video.requestFullscreen();
      } else if (video.webkitRequestFullscreen) {
        video.webkitRequestFullscreen();
      } else if (video.mozRequestFullScreen) {
        video.mozRequestFullScreen();
      } else if (video.msRequestFullscreen) {
        video.msRequestFullscreen();
      }
    },
    handleFullscreenChange(e) {
      const video = e.target;
      const isFullscreen = document.fullscreenElement || document.webkitFullscreenElement || document.mozFullScreenElement || document.msFullscreenElement;
      if (isFullscreen) {
        // 
        video.muted = false;
        video.loop = false;
      } else {
        // 
        video.muted = true;
        video.loop = true;
        video.pause();
        video.currentTime = 0;
      }
    },
    handleVideoHover(e, isEnter) {
      const video = e.target;
      if (isEnter) {
        video.play().catch(() => {});
      } else {
        video.pause();
        video.currentTime = 0;
      }
    }
  }
};